d various image annotation workshops with crisis managers to identify the limitations in our approach and understand how we could iterate on the design of our ML methodology. This evaluation procedure thus enabled us to evaluate the use of image classification models in assisting crisis managers using quantitative metrics as well as qualitatively through the feedback we got through the image annotation workshops. This evaluation directly influenced our approach for devising the Text Analysis Module. ")])])])]),_c('div',{staticClass:"carousel-item cc-carousel-item"},[_c('div',{staticClass:"carousel-text"},[_c('h5',[_vm._v("Image Datasets & Train/Dev/Test Splits for Model Development & Evaluation")]),_c('div',{staticClass:"row justify-content-around"},[_c('div',{staticClass:"carousel-text col-12 col-md-9"},[_c('p',[_vm._v(" Since we used CNN models, specifically the EfficientNet-B1 architecture pretrained on ImageNet, we wanted to make use of large open-source Twitter crisis image datasets for finetuning the models to perform the classification tasks of Damage Severity (DS), Humanitarian Categories (HC), Informativeness (IN), and Flood Presence (FP). ")]),_c('h6',[_vm._v("Open-source Consolidated Crisis Image Datasets")]),_c('p',[_vm._v(" For the DS, HC, and IN tasks, we made use of the open-source train/dev/test splits made available at "),_c('a',{attrs:{"href":"https://crisisnlp.qcri.org/crisis-image-datasets-asonam20","target":"_blank"}},[_vm._v("CrisisNLP")]),_vm._v(". We train the models which perform these tasks using the train and dev splits. For evaluation, we make use of the respective test splits for each task. ")]),_c('h6',[_vm._v("Flood Presence Task Creation and Dataset Formation")]),_c('p',[_vm._v(" In this work we focus on flood-crisis events, thus we used various open-source image datasets which have flood-adjacent labels and map them to the binary labels of \"Flood\"/\"Not Flood\". Using the resulting dataset, we create randomized, non-overlapping Tra